@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.kc-html {
  background-color: white;
  font-size: unset;
}

#kc-form {
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
}

#kc-form-wrapper {
  width: 100%;
  padding: 0;
}

#kc-header-wrapper {
  width: 100%;
  background-image: url('../../assets/teste-logo.png');
  background-repeat: no-repeat;
  background-position: center;
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#kc-header-wrapper > span {
  display: none;
}

.input-class {
  width: 100%;
  background-color: #f2f2f2 !important;
  border-radius: 8px !important;
  padding: 0.625rem 0.813rem !important;
  border: 1px solid rgba(0, 0, 0, 0.1) !important;
  font-size: 1rem !important;
}

.button-class {
  width: 100%;
  background-color: #0ea5e9 !important;
  border-radius: 8px;
  padding: 0.8rem 1rem !important;
  border: 1px solid rgba(0, 0, 0, 0.1) !important;
  background-image: none !important;
  cursor: pointer;
  color: white;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.8rem;
  margin-bottom: 1rem;
}

.label-class > span {
  font-size: 1rem !important;
  font-weight: 700 !important;
  color: #000000 !important;
}

a {
  color: #0ea5e9;
  text-decoration: underline;
  text-transform: uppercase;
  font-size: 0.875rem;
  font-weight: 700;
}

a:hover {
  color: #0369a1;
  text-decoration: underline;
  text-transform: uppercase;
  font-weight: 700;
}

#zocial-facebook,
#zocial-github,
#zocial-google,
#zocial-microsoft,
#zocial-stackoverflow,
#zocial-linkedin,
#zocial-twitter {
  background-image: none;
  border: 1px solid black;
  box-shadow: none;
  text-shadow: none;
  background-color: unset;
  border-radius: 8px;
  padding: 4px 0;
  margin: 8px 0px;
}

#kc-content-wrapper .row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

#kc-social-providers {
  width: 100% !important;
  padding: 0;
}

#kc-page-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #000000;
}

#gender {
  padding: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-size: 0.875rem;
}

#privacyPolicy {
  accent-color: rgba(51, 51, 51, 1);
}

#legalAge {
  accent-color: rgba(51, 51, 51, 1);
}
